*{box-sizing:border-box;margin:0;padding:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.list{margin:50px 0;padding:20px;border-radius:5px;list-style-type:none;display:flex;flex-direction:column;border:1px solid gray;background-color:rgba(0,0,0,0.2);box-shadow:0 20px 20px -5px rgba(0,0,0,0.4)}.list__item{width:300px;height:34px;background-color:#b3b3b3;border:1px solid #4d4d4d;display:flex;align-items:center;margin:1px 0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.list__item:hover{background-color:#bfbfbf}.list__item.slideRight{-webkit-animation:slideRight 400ms forwards;animation:slideRight 400ms forwards}.list__item.slideDown{margin-top:36px}.list__item.dummy{visibility:hidden}.list__item-title{flex-grow:1;display:flex;align-items:center;font-weight:100}.list__item-title h1{font-size:1em;flex-grow:1;font-weight:inherit}.list__item-title span{font-size:0.8em}.list__item-icon{padding:4px;width:24px;height:24px;margin:0 10px 0 4px}.list__item-grab{margin-left:10px;width:32px;height:32px;display:flex;justify-content:center;align-items:center}.list__item-grab::after{content:'☰'}.list__item-grab:hover{transition:100ms;color:#fff;cursor:pointer}@-webkit-keyframes slideLeft{0%{margin-left:0;background-color:#b3b3b3}100%{margin-left:-40px;background-color:#9cf76e}}@keyframes slideLeft{0%{margin-left:0;background-color:#b3b3b3}100%{margin-left:-40px;background-color:#9cf76e}}@-webkit-keyframes slideRight{0%{margin-left:-40px;background-color:#9cf76e}100%{margin-left:0;background-color:#b3b3b3}}@keyframes slideRight{0%{margin-left:-40px;background-color:#9cf76e}100%{margin-left:0;background-color:#b3b3b3}}

.app{background-color:#282c34;min-width:100vw;min-height:100vh;display:flex;justify-content:center;align-items:flex-start;padding-top:20px}

